:root {
    --clr-white: #fff;
    --clr-black: #000;
    --clr-main-purple: rgb(81, 39, 158);
    --clr-light-green-cyan: #03f5c9;
    --clr-vivid-green-cyan: #00d084;
    /*--font-main: 'Catamaran', sans-serif;*/
    /*--font-main: 'Montserrat', sans-serif;*/
    /*--font-main:  'Kanit', sans-serif;*/
    /*--font-main: 'Noto Sans Egyptian Hieroglyphs', sans-serif;*/
    --font-main: 'Poppins', sans-serif;
    --font-size-h1: 58px;
    --font-size-h2: 42px;
    --font-size-p: 18px;
    --font-size-span-title: 60px;
    --font-size-span-title-footer: 52px;
    --font-size-span-join: 26px;
    --font-size-href: 17px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font-main);
    scroll-behavior: smooth;
}


/*******  GENERAL *******/

img {
    display: block;
    height: auto;
    width: 100%;
}

li,
a {
    text-decoration: none;
    list-style: none;
}

a {
    color: var(--clr-white);
    font-size: var(--font-size-href);
    font-weight: 600;
    transition: .2s color ease;
    position: relative;
}

a:hover {
    color: var(--clr-light-green-cyan);
}

a:hover::after {
    left: 50%;
    width: 100%;
}

a::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 0.5px;
    width: 0px;
    /* or 100px */
    top: 120%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-bottom: 6px solid var(--clr-light-green-cyan);
    transition: .2s left ease, .2s width ease;
}

p {
    line-height: 1.3;
    font-size: var(--font-size-p);
}

section {
    display: flex;
    justify-content: center;
    align-items: center;
}

.row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 30px;
}

.left,
.right {
    display: flex;
    justify-content: end;
    align-items: center;
    width: 100%;
}

.left {
    opacity: 0;
    position: relative;
    left: -40px;
    transition: all .8s ease-out;
}

.right {
    opacity: 0;
    position: relative;
    right: -40px;
    transition: all .8s ease-out;
}

.title {
    font-weight: bold;
    font-size: var(--font-size-span-title);
}

section {
    padding: 4em 0;
}

.show-left {
    opacity: 1;
    left: 0;
}

.show-right {
    opacity: 1;
    right: 0;
}

.show-up {
    opacity: 1;
    bottom: 0;
}

h1 {
    font-size: var(--font-size-h1);
    margin-bottom: 20px;
    color: var(--clr-main-purple);
}

h2 {
    font-size: var(--font-size-h2);
    color: var(--clr-main-purple);
}

h1,
h2,
p {
    opacity: 0;
    position: relative;
    bottom: -40px;
    transition: all .6s ease-out;
}

header {
    background: url(/src/background/bg-purple.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    color: var(--clr-white);
}

input {
    outline: none;
    height: 55px;
    width: 500px;
    border: 1px solid #e1e1e1;
    padding: 0 1em;
}

.button {
    background-color: var(--clr-main-purple);
    padding: .8em 2em;
    border-radius: 5px;
    color: var(--clr-white);
    cursor: pointer;
    transition: .2s background-color ease;
}

.button:hover {
    background-color: rgb(111, 53, 219);
}
.uil{
    cursor: pointer;
}

.uil-bars {
    padding: 0;
    margin: 0;
    font-size: 28px;
    border-radius: 50%;
    color: var(--clr-white);
    display: none;
    transition: .2s color ease;
}

.uil-bars:hover {
    color: var(--clr-light-green-cyan);
}

.uil-bars.active {
    color: var(--clr-light-green-cyan);
}

.uil-triangle {
    position: absolute;
    transform: rotate(180deg);
    right: 2%;
}


/***** NAVBAR ******/

.navbar {
    position: fixed;
    display: flex;
    padding: 1em 10%;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    z-index: 1000;
    transition: .2s background-color ease, .2s padding ease;
}

.navbar.active {
    padding: .5em 10%;
    background-color: rgb(0, 0, 0, 0.5);
}

.whoweare {
    background: url(/src/background/bg-darkpurple.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: var(--clr-white);
    padding: 0;
    max-height: 540px;
}

.text-whoweare {
    width: 80%;
}

.text-whoweare p {
    width: 100%;
}

.h1-modified {
    text-align: start;
    text-transform: unset;
    color: var(--clr-white);
    font-size: 80px;
    margin-bottom: 80px;
    position: relative;
}

.h1-modified::after {
    content: "somos?";
    top: 70%;
    left: 32%;
    position: absolute;
}

.logo-navbar {
    width: 200px;
}

.list-items {
    display: flex;
    gap: 20px;
}

.join-us {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 10em 0em 7em 0;
}

.header-row {
    width: 80%;
}

.text-header {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.joinus-title {
    font-size: var(--font-size-span-join);
    color: var(--clr-main-purple);
    font-weight: bold;
}

.join-form {
    display: flex;
    color: var(--clr-black);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--clr-white);
    padding: 2em 1.5em 4em 1.5em;
    border-radius: 20px;
    min-width: 320px;
    gap: 10px;
    box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.2);
}
.list-input {
    display: flex;
    width: 500px;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.dropdown{
    cursor: pointer;
}
.options-input {
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    color: var(--clr-black);
    width: 100%;
    position: absolute;
    top: 100%;
    transition: .2s padding ease;
    background-color: #fff;
    z-index: 999;
}
.option{
    background-color: unset;
    cursor: pointer;
    font-size: 0;
    transition: .2s background-color ease, .2s font-size ease;
}
.option:hover{
    background-color: #e9e9e9;
}
.options-input.active{
    padding: 1em 1em;
    border-bottom: 1px solid #e1e1e1;
    border-left: 1px solid #e1e1e1;
    border-right: 1px solid #e1e1e1;
}
.option.active{
    font-size: 14px;
}
.alert{
    width: 100%;
    position: relative;
    left: 0%;
    color: red;
    font-size: 12px;
}

/***** MAIN ******/

.benefits {
    height: 100%;
    padding: 4em 0 2em 0;
}

.container-bg,
section {
    width: 100%;
}

.container-bg {
    background: url(/src/background/bg-gray.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.convention {
    background-color: #ececec;
}

.redirect {
    background: url(/src/background/bg-purple.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: var(--clr-white);
    padding: 6em 0;
    text-align: center;
}

.container-section {
    display: flex;
    flex-direction: column;
    width: 80%;
    height: auto;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding: 1em 0;
}

.card-benefits {
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.card-image {
    display: flex;
    justify-content: baseline;
    align-items: flex-end;
    margin-bottom: 50px;
    height: 50%;
}

.card-image-telescope {
    max-height: 400px;
}

.video-responsive {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

iframe {
    height: 100%;
    width: 100%;
}

.purpose-box {
    border-radius: 20px;
    width: 70%;
    height: 500px;
}

.collection-proyects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.image-proyect {
    position: relative;
}

.image-proyect img {
    transform: scale(1);
    transition: .2s transform ease;
}

.image-proyect img:hover {
    transform: scale(1.02);
}

.image-proyect p {
    color: var(--clr-white);
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: var(--font-size-p);
    font-weight: bold;
}

.proyects-column,
.convention-column,
.talk-column,
.purpose-column {
    width: 85%;
    text-align: center;
}

.purpose-column p {
    width: 70%;
}

.collection-proyects img {
    max-width: 400px;
}

.rounded-images {
    flex-wrap: wrap;
    height: auto;
    width: 100%;
    display: flex;
    gap: 20px;
    justify-content: space-evenly;
}

.rounded-images img {
    height: 200px;
    width: 200px;
    border-radius: 50%;
}

.form-button {
    text-align: center;
    margin-top: 20px;
    width: 100%;
    font-weight: bold;
    font-size: var(--font-size-p);
    background-color: var(--clr-main-purple);
}

.title-footer {
    font-size: var(--font-size-span-title-footer);
    font-weight: bold;
}

.redirect-button {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 320px;
    max-width: 600px;
    font-weight: bold;
    padding: .5em 1em;
    font-size: var(--font-size-span-join);
    letter-spacing: 1.2px;
    box-shadow: 10px 10px 10px rgb(0, 0, 0, .3);
}


/******** FOOTER *********/

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--clr-black);
    padding: 2em 0em;
}

.footer-logo {
    width: 230px;
}


/**11 pulgadas: 1366×768 píxeles. 13 pulgadas: 1440×900 píxeles.*/

@media (max-width: 1640px) {
    .benefits h1 {
        margin-bottom: 0px;
    }
    .h1-modified {
        font-size: 70px;
    }
    .logo-navbar {
        width: 180px;
    }
    .navbar.active {
        padding: .2em 10%;
    }
     :root {
        --font-size-h1: 56px;
        --font-size-h2: 36px;
        --font-size-p: 16px;
        --font-size-span-title: 50px;
        --font-size-span-title-footer: 48px;
        --font-size-span-join: 24px;
        --font-size-href: 15px;
    }
    input,
    .list-input {
        width: 380px;
        height: 45px;
    }
    .purpose-box {
        width: 90%;
    }
    .card-image-telescope {
        max-height: 350px;
    }
    .container-bg {
        background-size: cover;
        background-position: center;
    }
}

@media (max-width: 1333px) {
    .card-image-telescope {
        max-height: 250px;
    }
}

@media (max-width: 1080px) {
    .card-image-telescope {
        max-height: unset;
    }
    .h1-modified {
        font-size: var(--font-size-h1);
    }
    .benefits h1 {
        margin-bottom: 100px;
    }
    .benefits {
        padding: 3em 0 0em 0;
    }
     :root {
        --font-size-h1: 50px;
        --font-size-h2: 36px;
        --font-size-p: 16px;
        --font-size-span-title: 50px;
        --font-size-span-join: 24px;
        --font-size-span-title-footer: 44px;
        --font-size-href: 15px;
    }
    .text-whoweare p {
        width: 100%;
    }
    .h1-modified {
        text-align: center;
        text-transform: unset;
        margin-bottom: 40px;
    }
    .h1-modified::after {
        content: " somos?";
        top: unset;
        left: unset;
        position: unset;
    }
    .whoweare {
        padding: 1em 0;
        max-height: unset;
    }
    .text-header {
        text-align: center;
    }
    .whoweare {
        text-align: center;
    }
    .title {
        text-align: center;
    }
    .row {
        flex-direction: column;
    }
    .card-image {
        margin-bottom: 0px;
    }
    .card-benefits {
        gap: 100px;
        justify-content: center;
    }
    .left,
    .right {
        justify-content: center;
    }
    .card-benefits {
        flex-wrap: wrap;
    }
    .collection-proyects img {
        max-width: 300px;
    }
    .purpose-box {
        width: 90%;
    }
}

@media (max-width: 700px) {
    .navbar {
        padding: 1em 5%;
    }
    .navbar.active {
        padding: .2em 5%;
    }
    .header-row {
        width: 95%;
    }
    .container-section {
        width: 90%;
    }
    .proyects-column,
    .convention-column,
    .talk-column,
    .purpose-column {
        width: 95%;
    }
     :root {
        --font-size-span-title: 52px;
    }
    .purpose-column p {
        width: 90%;
    }
    .purpose-box {
        width: 90%;
        height: 200px;
    }
}

@media (max-width: 580px) {
    .benefits h1 {
        margin-bottom: 50px;
    }
     :root {
        --font-size-h1: 44px;
        --font-size-h2: 30px;
        --font-size-p: 16px;
        --font-size-span-title: 40px;
        --font-size-span-join: 24px;
        --font-size-href: 16px;
    }
    input,
    .list-input {
        width: 320px;
    }
    .logo-navbar,
    .footer-logo {
        width: 150px;
    }
    .uil-bars {
        display: unset;
    }
    .link-items {
        color: var(--clr-main-purple);
        font-size: 0;
        transition: .2s font-size ease;
    }
    .list-items {
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 50px;
    }
    .navbar-items {
        position: absolute;
        display: flex;
        flex-direction: column;
        background-color: rgb(255, 255, 255, 1);
        left: 0;
        top: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 0;
        transition: .2s height ease;
    }
    .navbar-items.active {
        height: 50vh;
        border-bottom: 2px solid var(--clr-black);
        border-top: 2px solid var(--clr-black);
    }
    .link-items.active {
        font-size: var(--font-size-href);
    }
}

@media (max-width: 420px) {
    input,
    .list-input {
        width: 300px;
    }
}